import { Component, OnInit } from "@angular/core";

import { TokenCookieService } from "src/app/core/service/token-storage-cookies.service";

import * as Highcharts from "highcharts";

@Component({
  selector: "app-analytics",
  templateUrl: "./analytics.component.html",
  styleUrls: ["./analytics.component.scss"],
})
export class AnalyticsComponent implements OnInit {
  monthsArray: any = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ];
  employeesArray: any = [
    49.9, 71.5, 106.4, 129.2, 144.0, 176.0, 135.6, 148.5, 216.4, 194.1, 95.6,
    54.4,
  ];
  maleVsFemaleOptions: any;
  male: any;
  female: number;
  employeeLeaveChart: any;
  currentUser: any
  constructor(private tokenCookieService: TokenCookieService) {
    this.currentUser = tokenCookieService.getUser().username;
  }
  ngOnInit() {
    this.showEmployeeChart();
  }
  barChartoptions: any;
  leaveDaysDash: any;

  transformMonths(originalData) {
    const periodmArray = [];
    for (const item of originalData) {
      const periodm = item.periodm.trim();
      periodmArray.push(periodm);
    }
    return periodmArray;
  }
  transformEmployeeData(originalData: any) {
    let employeeArray = new Array<any>();
    for (const item of originalData) {
      employeeArray.push(Number(item.employeeCount));
    }
    return employeeArray;
  }
  transformSalaryData(originalData: any) {
    let salaryArray = new Array<any>();
    for (const item of originalData) {
      salaryArray.push(item.grosspay);
    }
    return salaryArray;
  }
  showEmployeeChart() {
    this.leaveDaysDash = {
      chart: {
        type: "column",
      },
      title: {
        text: "Your average on-Leave days per Month",
      },
      subtitle: {
        text: "ERP",
      },
      credits: {
        enabled: false,
      },
      xAxis: {
        categories: this.monthsArray,
        crosshair: true,
      },
      yAxis: {
        min: 0,
        title: {
          text: "LEAVE Days",
        },
      },
      tooltip: {
        headerFormat:
          '<span style = "font-size:10px">{point.key}</span><table>',
        pointFormat:
          '<tr><td style = "color:{series.color};padding:0">{series.name}: </td>' +
          '<td style = "padding:0"><b>{point.y:.1f}</b></td></tr>',
        footerFormat: "</table>",
        shared: true,
        useHTML: true,
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0,
        },
      },
      series: [
        {
          name: "Employee",
          color: "#157d05",
          data: this.employeesArray,
        },
      ],
    };
    Highcharts.chart("leaveDaysDash", this.leaveDaysDash);
  }
}
