import { HttpParams } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Subject, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { AnalysisService } from "src/app/erp-hr/data/configuration-services/analysis.service";
import { SupplierCategoriesService } from "src/app/erp-suppliersmanagement/data/supplier-categories.service";

@Component({
  selector: "app-gen-widgets",
  templateUrl: "./gen-widgets.component.html",
  styleUrls: ["./gen-widgets.component.scss"],
})
export class GenWidgetsComponent implements OnInit {
  resData: any;
  getYear: number;
  getMonth: any;
  year: any;
  month: any;
  totalNssf: any;
  totalNhif: any;
  totalPaye: any;
  totalGrosspay: any;
  totalNetpay: any;
  totalEmployees: any;
  currentDate = new Date();
  $destroy: Subject<boolean> = new Subject<boolean>();
  loading: boolean = false;
  totalLeaveDays: number = 0;
  annualLeaveDaysBal: number = 0;
  avgAppraisalScore: number = 0;
  currQuarterAppraisalScore: number = 0;
  constructor(
    private analysisAPI: AnalysisService,
    private tokenStorageService: TokenStorageService
  ) {}
  ngOnDestroy(): void {}
  ngOnInit() {
    this.getAnalysisSummary();
  }
  getAnalysisSummary() {
    let params = new HttpParams()
      .set("empPf", this.tokenStorageService.getEmppf())
      .set("Year", this.currentDate.getFullYear());
    this.analysisAPI.getEmployeeAnalysis(params).subscribe(
      (res) => {
        this.resData = res;
        this.totalLeaveDays = this.resData.totalLeaveDays || 0;
        this.annualLeaveDaysBal = this.resData.annualLeaveDaysBal || 0;
        this.avgAppraisalScore = this.resData.avgAppraisalScore || 0;
        this.currQuarterAppraisalScore =
          this.resData.currQuarterAppraisalScore || 0;
      },
      (err) => {}
    );
  }

  onTotalLeaveDays() {
    console.log("onTotalLeaveDays card clicked");
  }

  leaveDaysBalance() {
    console.log("leaveDaysBalance card clicked");
  }

  averageAppraisalBalance() {
    console.log("averageAppraisalBalance card clicked");
  }

  currentAppraisal() {
    console.log("currentAppraisal card clicked");
  }
}
