import { HttpParams } from '@angular/common/http';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { TokenStorageService } from 'src/app/core/service/token-storage.service';
import { AnalysisService } from 'src/app/erp-hr/data/configuration-services/analysis.service';

@Component({
  selector: 'app-detail-leave-grid',
  templateUrl: './detail-leave-grid.component.html',
  styleUrls: ['./detail-leave-grid.component.scss']
})
export class DetailLeaveGridComponent implements OnInit, OnDestroy {
  displayedColumns: string[] = [
    'index',
    'fromDate',
    'toDate',
    'days',
    'remainingdays',
    'handOverPF'
  ];      
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  loading: boolean;
  year = new Date().getFullYear();
  resData: any;
  constructor(
    private analysisAPI: AnalysisService,
    private tokenStorageService: TokenStorageService
  ) {
   }
   ngOnDestroy(): void {
  }
  ngOnInit() {
    this.getData();
  }
  getData() {
    let params = new HttpParams()
      .set("empPf", this.tokenStorageService.getEmppf())
      .set("Year",this.year);
    this.analysisAPI
      .getEmployeeAnalysis(params).subscribe(res=>{
        this.resData = res;
        this.dataSource = new MatTableDataSource(this.resData.onLeaveDays);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, err=>{
      });
  }

  isValidYear(input: any): boolean {
    const year = parseInt(input, 10);
    return year >= 1000 && year <= 9999;
  }
  onSelectedYear(event: any) {
    if(this.isValidYear(event.target.value)){
      this.year = event.target.value;
    this.getData();
    }else{
      // this.notificationAPI.alertWarning("Invalid Year")
    }
  }

  // getData() {
  //   this.loading = true;
  //   let params = new HttpParams()
  //   .set('empPF',this.tokenService.getUser().empPfNo)
  //   .set('periody',this.year);
  //   this.payrollService.getPayslipPerEmpPF(params).subscribe(res=>{
  //     this.loading = false;
  //     this.data = res;
  //     if(this.data.statusCode == '302'){
  //       this.data = this.data.entity
  //       if(this.data.length != null || this.data.length>0) {
  //       this.dataSource = new MatTableDataSource(this.data);
  //       this.dataSource.paginator = this.paginator;
  //       this.dataSource.sort = this.sort;
  //       }
  //     }else{
  //       this.notificationAPI.alertWarning(this.data.message);
  //       this.dataSource = new MatTableDataSource(this.data.entity);
  //     }
  //   }, err=>{
  //   });
  // }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}



