import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";

import { TokenCookieService } from "src/app/core/service/token-storage-cookies.service";
import { element } from "protractor";
import { ItemCategoryService } from "src/app/erp-inventory/data/services/item-category.service";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.sass"],
})
export class DashboardComponent implements OnInit {
  passwordFlag: any;
  isPageReloaded = false;
  currentUser: any;

  constructor(private tokenCookieService: TokenCookieService) {}

  ngOnInit(): void {
    this.passwordFlag = this.tokenCookieService.getUser().systemGenPassword;
    this.currentUser = this.tokenCookieService.getUser().username;
  }

}
